export default{
	data(){
		return{
			title:'泉州综合出行',
			versionNum:'Version ',
			functionIntroduction:'功能介绍',
			checkVersion:'检查新版本',
			agreement:'《软件许可及服务协议》',
			privacy:'《隐私保护指引》',
			copyright1:'途游信息科技 版权所有',
			copyright2:'Copyright©2020 Journey',
			copyright3:'All Rights Reserved',
			version:'',
		}
	},
	onLoad(){
		this.version=plus.runtime.version;
	},
	methods:{
		functionClick(){
			uni.showToast({
				icon:'none',
				title:'功能介绍'
			})
		},
		checkClick(){
			var that=this;
			uni.request({
				url:that.$GrzxInter.Interface.SearchVersion.value,
				data:{
					model:'旅客端',
				},
				method:that.$GrzxInter.Interface.SearchVersion.method,
				success(res) {
					console.log(res)
					if(that.version!=res.data.data.version){
						uni.showModal({
						    content: '是否下载新版本',
						    success: (e)=>{
						    	if(e.confirm){
									//plus.runtime.openURL(res.data.DownLoadUrl);




									uni.showToast({
										title:'暂无法下载新版本'
									})

						    	}
						    }
						});
					}else{
						uni.showToast({
							icon:'none',
							title:'当前版本为最新版本'
						})
					}
				}
			})
		},
		agreementClick(){
			uni.showToast({
				icon:'none',
				title:'软件许可及服务协议'
			})
		},
		privacyClick(){
			uni.showToast({
				icon:'none',
				title:'隐私保护指引'
			})
		},
	}
}
